/* --- generic elements --- */
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  font-size: 105%;
  line-height: 1.5em;
  background-color: #ddd;
  color: #666677;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:selection {
  background-color: #245672;
  color: #ffffff;
}
::selection {
  background-color: #245672;
  color: #ffffff;
}
h1 {
  font-size: 2em;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  margin-bottom: 0.5em;
}
h1.h--light,
h2.h--light,
h3.h--light,
h4.h--light,
h5.h--light,
h6.h--light {
  font-weight: 600;
}
p {
  font-size: 1em;
  max-width: 35em;
  margin-bottom: 20px;
  color: #666677;
}
p small {
  display: block;
  line-height: 1.5em;
}
a {
  color: #44a1e0;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(51, 51, 68, 0.05);
}
a:hover {
  color: #245672;
}
a:hover,
a:active,
a:focus,
a:link,
a:visited {
  text-decoration: none;
}
hr {
  margin: 20px 0;
  border: none;
  border-bottom: 1px solid #ccccdd;
}
hr.subtle {
  border-color: rgba(204, 204, 221, 0.5);
}
hr.dark {
  border-color: #245672;
}
/* --- extended/condensed text --- */
.condensed {
  display: none;
}
.extended {
  display: inline;
}
.u-sm-show {
  display: none;
}
@media screen and (max-width: 768px) {
  .extended {
    display: none;
  }
  .condensed {
    display: inline;
  }
  .u-sm-show {
    display: block;
  }
}
/* --- Hide elements when printing/saving as PDF --- */
@media print {
  .no-print,
  .no-print * {
    display: none !important;
  }
}
