/* --- badge --- */
.badge {
  display: inline-block;
  background-color: #eeeeee;
  color: #667;
  text-align: center;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  overflow: hidden;
}
.badge img {
  max-width: 100%;
}
.badge.badge-circle-small {
  width: 20px;
  height: 20px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}
.badge.badge-circle {
  width: 40px;
  height: 40px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}
.badge.badge-circle-large {
  width: 60px;
  height: 60px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}
.badge.badge-circle-xlarge {
  width: 80px;
  height: 80px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}
.badge.badge-user img {
  display: block;
}
.badge.badge-large {
  width: 80px;
  height: 80px;
  padding: 18px 10px;
}
.badge.badge-support {
  background-color: #ccccdd;
  color: #ffffff;
  z-index: 3;
}
.badge.badge-support label {
  font-size: 0.75em;
}
.badge.badge-support .value {
  font-size: 1.5em;
  font-weight: bold;
}
.badge.badge-support.badge-new-empty {
  background-color: #ffffff;
  border: 1px solid #ccccdd;
  color: #44a1e0;
}
.badge.badge-support.badge-new {
  background-color: #44a1e0;
}
.badge.badge-support.badge-in-progress {
  background-color: #c678ea;
}
.badge.badge-support.badge-solved {
  background-color: #27ae60;
}
.badge.badge-support.isGrowing {
  -webkit-animation: isGrowing 0.3s ease-in-out;
  -moz-animation: isGrowing 0.3s ease-in-out;
  animation: isGrowing 0.3s ease-in-out;
  -webkit-transform-origin: center center;
  -moz-transform-origin: center center;
  transform-origin: center center;
}
.badge.badge-support.isSlinking {
  -webkit-animation: isSlinking 0.3s ease-in-out;
  -moz-animation: isSlinking 0.3s ease-in-out;
  animation: isSlinking 0.3s ease-in-out;
  -webkit-transform-origin: center center;
  -moz-transform-origin: center center;
  transform-origin: center center;
}
.badge.badge-support-outline {
  padding-top: 8px;
}
.badge.badge-support-outline.badge-new {
  border: 1px solid #44a1e0;
  color: #44a1e0;
}
.badge.badge-support-outline.badge-in-progress {
  border: 1px solid #c678ea;
  color: #c678ea;
}
.badge.badge-support-outline.badge-solved {
  border: 1px solid #27ae60;
  color: #27ae60;
}
.badge.badge-image {
  width: 50px;
  height: 50px;
  background-color: #eeeeee;
  background-size: cover;
}
.badge.badge-image img {
  max-height: 100%;
  max-width: 100%;
}
.badge.badge-image .ion {
  margin: 7px 0;
}
.badge.badge-image .ion-onbadge {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  width: 40px;
  height: 40px;
  margin-left: -20px;
  margin-top: -20px;
  background-color: rgba(36, 86, 114, 0.4);
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
}
.badge.badge-image .ion-onbadge:hover,
.badge.badge-image .ion-onbadge:focus {
  background-color: #44a1e0;
}
.label {
  display: inline-block;
  padding: 10px 15px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  background-color: #9999aa;
  font-size: 1.1em;
  line-height: 20px;
  font-weight: bold;
  color: #ffffff;
}
.label.label-new-empty {
  background-color: #ffffff;
  border: 1px solid #ccccdd;
  color: #44a1e0;
}
.label.label-new {
  background-color: #1f7bb9;
}
.label.label-in-progress {
  background-color: #aa36e0;
}
.label.label-solved {
  background-color: #19703e;
}
@-webkit-keyframes isGrowing {
  0% {
    -webkit-transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.25);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
@-moz-keyframes isGrowing {
  0% {
    -moz-transform: scale(1);
  }
  50% {
    -moz-transform: scale(1.25);
  }
  100% {
    -moz-transform: scale(1);
  }
}
@keyframes isGrowing {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes isSlinking {
  0% {
    -webkit-transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.85);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
@-moz-keyframes isSlinking {
  0% {
    -moz-transform: scale(1);
  }
  50% {
    -moz-transform: scale(0.85);
  }
  100% {
    -moz-transform: scale(1);
  }
}
@keyframes isSlinking {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.85);
  }
  100% {
    transform: scale(1);
  }
}
/* --- button --- */
.btn {
  position: relative;
  display: inline-block;
  height: 40px;
  padding: 5px 15px;
  background-color: #9999aa;
  border: 1px solid transparent;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  color: #ffffff;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  font-size: 1em;
  font-weight: 600;
  letter-spacing: 0.05em;
  line-height: 30px;
  cursor: pointer;
  vertical-align: top;
  text-align: center;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}
.btn:hover {
  background-color: #7d7d93;
  color: #ffffff;
}
.btn:focus {
  outline: none;
  text-decoration: none;
  color: #ffffff;
}
.btn:active {
  background-color: #707086;
  text-decoration: none;
  color: #ffffff;
  -webkit-transform: translateY(2px);
  -moz-transform: translateY(2px);
  -o-transform: translateY(2px);
  -ms-transform: translateY(2px);
  transform: translateY(2px);
}
.btn .ion-15x {
  line-height: 1em;
}
.btn.btn-lg {
  padding: 10px 20px;
  height: 50px;
  line-height: 25px;
  font-size: 1.25em;
  letter-spacing: 0;
}
.btn.btn-sm {
  padding: 5px 10px;
  height: 30px;
  line-height: 20px;
  letter-spacing: 0;
  font-size: 0.8em;
}
.btn.btn-circle {
  width: 40px;
  height: 40px;
  padding: 5px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
}
.btn.btn-circle.btn-sm {
  width: 25px;
  height: 25px;
  padding: 3px;
  line-height: 19px;
  -webkit-border-radius: 13px;
  -moz-border-radius: 13px;
  border-radius: 13px;
}
.btn.isBusy:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(68, 161, 224, 0.75);
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.btn.isBusy:after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -16px;
  margin-left: -7px;
  content: "\f29a";
  padding-top: 1px;
