input[type="password"].error:focus,
textarea.error:focus,
select.error:focus {
  -webkit-box-shadow: 0 0 0 2px #e74c3c;
  -moz-box-shadow: 0 0 0 2px #e74c3c;
  box-shadow: 0 0 0 2px #e74c3c;
}
