}
input[disabled] {
  color: #ccccdd;
}
input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  pointer-events: none;
  cursor: pointer;
}
input[type="checkbox"] + label:before {
  content: "\f372";
  display: inline-block;
  float: left;
  margin-right: 10px;
  font-family: "Ionicons";
  font-size: 1.5em;
}
input[type="checkbox"]:checked + label:before {
  content: "\f374";
}
input[type="checkbox"]#location + label:before {
  font-size: 1.2em;
}
input[type="radio"] {
  position: absolute;
  left: -9999px;
}
input[type="radio"] + label:before {
  content: "\f3a6";
  display: inline-block;
  float: left;
  margin-right: 10px;
  font-family: "Ionicons";
  font-size: 1.5em;
}
input[type="radio"]:checked + label:before {
  content: "\f3a7";
}
