    -webkit-transform: translateX(-260px);
    -moz-transform: translateX(-260px);
    -o-transform: translateX(-260px);
    -ms-transform: translateX(-260px);
    transform: translateX(-260px);
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all all 0.3s ease-in-out ease-out;
    -moz-transition: all all 0.3s ease-in-out ease-out;
    -o-transition: all all 0.3s ease-in-out ease-out;
    transition: all all 0.3s ease-in-out ease-out;
  }
  #menu-drawer .menu {
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all all 0.3s ease-in-out ease-out;
    -moz-transition: all all 0.3s ease-in-out ease-out;
    -o-transition: all all 0.3s ease-in-out ease-out;
    transition: all all 0.3s ease-in-out ease-out;
    opacity: 0;
  }
  #menu-drawer li {
    display: block;
    margin-right: 0;
  }
  #menu-drawer li.active a {
    border-bottom: none;
    border-left-color: #ffffff;
  }
  #menu-drawer li + li {
    border-top: 1px solid rgba(51, 51, 68, 0.2);
  }
  #menu-drawer a {
    display: block;
    height: 60px;
    max-width: 100%;
    padding: 15px;
    text-align: left;
    border-top: 0;
    border-left: 2px solid transparent;
  }
  #menu-drawer a .ion {
    display: inline-block;
    float: left;
    width: 30px;
    margin-right: 20px;
    margin-top: 4px;
    text-align: center;
  }
  #menu-drawer a .text {
    font-size: 1.1em;
    line-height: 30px;
  }
  #navbutton {
    display: inline-block;
    padding: 15px 10px;
  }
  .menu-isOpen #navbutton .ion-navicon {
    display: none;
  }
  .menu-isOpen #navbutton .ion-android-close {
    display: block;
  }
}
/* --- panel/mask --- */
#panel {
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all all 0.3s ease-in-out ease-out;
  -moz-transition: all all 0.3s ease-in-out ease-out;
  -o-transition: all all 0.3s ease-in-out ease-out;
  transition: all all 0.3s ease-in-out ease-out;
}
#panel-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  background-color: rgba(68, 161, 224, 0.75);
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all all 0.3s ease-in-out ease-out;
  -moz-transition: all all 0.3s ease-in-out ease-out;
  -o-transition: all all 0.3s ease-in-out ease-out;
  transition: all all 0.3s ease-in-out ease-out;
}
@media screen and (max-width: 768px) {
  #panel-mask {
    display: block;
  }
  .menu-isOpen {
    overflow: hidden;
  }
  .menu-isOpen #menu-drawer {
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -o-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
  .menu-isOpen #menu-drawer .menu {
    opacity: 1;
  }
  .menu-isOpen #panel {
    -webkit-transform: translateX(260px);
    -moz-transform: translateX(260px);
    -o-transform: translateX(260px);
    -ms-transform: translateX(260px);
    transform: translateX(260px);
  }
  .menu-isOpen #panel-mask {
    opacity: 1;
    pointer-events: all;
  }
}
