#slides {
  position: relative;
  max-height: 300px;
  margin-right: 10px;
}
#slides .slidesjs-navigation {
  position: absolute;
  top: 50%;
  z-index: 10;
  display: inline-block;
  width: 60px;
  height: 60px;
  margin-top: -30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0;
  line-height: 60px;
}
#slides .slidesjs-navigation:before {
  content: "";
  font-size: 24px;
  font-family: Ionicons;
}
#slides .slidesjs-navigation:hover {
  background-color: rgba(68, 161, 224, 0.5);
  color: #ffffff;
}
#slides .slidesjs-navigation:active {
  background-color: #44a1e0;
}
#slides .slidesjs-navigation.slidesjs-previous {
  left: 0;
  -webkit-border-top-right-radius: 30px;
  -webkit-border-bottom-right-radius: 30px;
  -webkit-border-bottom-left-radius: 0;
  -webkit-border-top-left-radius: 0;
  -moz-border-radius-topright: 30px;
  -moz-border-radius-bottomright: 30px;
  -moz-border-radius-bottomleft: 0;
  -moz-border-radius-topleft: 0;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  -moz-background-clip: padding-box;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}
#slides .slidesjs-navigation.slidesjs-previous:before {
  content: "\f124";
}
#slides .slidesjs-navigation.slidesjs-next {
  right: 0;
  -webkit-border-top-right-radius: 0;
  -webkit-border-bottom-right-radius: 0;
  -webkit-border-bottom-left-radius: 30px;
  -webkit-border-top-left-radius: 30px;
  -moz-border-radius-topright: 0;
  -moz-border-radius-bottomright: 0;
  -moz-border-radius-bottomleft: 30px;
  -moz-border-radius-topleft: 30px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
  -moz-background-clip: padding-box;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}
#slides .slidesjs-navigation.slidesjs-next:before {
  content: "\f125";
}
#slides:hover .slidesjs-navigation {
  color: #ffffff;
}
#slides .slidesjs-pagination {
  position: absolute;
  bottom: 10px;
  width: 100%;
  z-index: 10;
  text-align: center;
}
#slides .slidesjs-pagination:before,
#slides .slidesjs-pagination:after {
  content: " ";
  display: table;
}
#slides .slidesjs-pagination:after {
  clear: both;
}
#slides .slidesjs-pagination li {
  list-style: none;
  display: inline-block;
  margin-right: 10px;
}
#slides .slidesjs-pagination li a {
  display: block;
  width: 10px;
  height: 10px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.5);
  font-size: 0;
}
#slides .slidesjs-pagination li a.active {
  background-color: #ffffff;
}
@media screen and (max-width: 768px) {
  #slides {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
.tag {
