  border-radius: 5px;
  font-size: 0.9em;
  line-height: 30px;
  font-weight: bold;
}
.tag .tag-close {
  display: inline-block;
  float: right;
  height: 32px;
  margin-top: -3px;
  margin-bottom: -3px;
  padding-top: 8px;
  border-left: 1px solid rgba(51, 51, 68, 0.2);
  line-height: 15px;
  color: rgba(255, 255, 255, 0.75);
}
.tag .tag-close:hover,
.tag .tag-close:focus {
  color: #ffffff;
}
.tag-collapsed {
  width: 12px;
  height: 12px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-transform: translateY(2px);
  -moz-transform: translateY(2px);
  -o-transform: translateY(2px);
  -ms-transform: translateY(2px);
  transform: translateY(2px);
  margin-right: 5px;
  padding: 0;
}
