.tooltip {
  position: absolute;
  bottom: 100%;
  left: 0;
  display: block;
  max-width: 200px;
  width: 200px;
  margin-bottom: 20px;
  padding: 10px 15px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  background-color: rgba(36, 86, 114, 0.6);
  color: #ffffff;
  font-size: 0.9em;
  line-height: 1.25em;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: opacity 0.2s ease;
  -o-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
  -webkit-transition: all opacity 0.2s ease ease-out;
  -moz-transition: all opacity 0.2s ease ease-out;
  -o-transition: all opacity 0.2s ease ease-out;
  transition: all opacity 0.2s ease ease-out;
}
.tooltip:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 10px;
  border: 7px solid transparent;
  border-top-color: rgba(36, 86, 114, 0.6);
}
.tooltip.tooltip-alignright {
  left: auto;
  right: 0;
}
.tooltip.tooltip-alignright:before {
  left: auto;
  right: 10px;
}
.tooltip.tooltip-compact {
  margin-bottom: 5px;
  padding: 5px 10px;
}
.hasTooltip {
  position: relative;
}
.hasTooltip:hover .tooltip {
  opacity: 1;
  pointer-events: all;
}
