/* --- rows (extends bootstrap's grid system) --- */
.row {
  max-width: 1100px;
  margin: 0 auto;
}
.row:before,
.row:after {
  content: " ";
  display: table;
}
.row:after {
  clear: both;
}
.row-nopadding > [class*="col-"] {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.col-xs-show {
  display: none;
}
@media screen and (max-width: 480px) {
  .col-xs-hide,
  .row .col-xs-hide {
    display: none;
  }
  .col-xs-show {
    display: inherit;
  }
}
@media screen and (max-width: 768px) {
  .col-sm-hide,
  .row .col-sm-hide {
    display: none;
  }
}
@media screen and (max-width: 320px) {
  .row:not(.row-nopadding) {
    margin-left: -10px;
    margin-right: -10px;
    width: calc(100% + 20px);
    max-width: 1120px;
  }
}
